<template>
  <div class="three-demo-1">
    <h1>Three.js WebXR examples</h1>
    <div id="xr-renderer-wrapper"></div>
  </div>
</template>

<script>
import * as THREE from 'three'

import { BoxLineGeometry } from 'three/addons/geometries/BoxLineGeometry.js'
import { XRButton } from 'three/addons/webxr/XRButton.js'
import { XRControllerModelFactory } from 'three/addons/webxr/XRControllerModelFactory.js'

export default {
  mounted() {
    const clock = new THREE.Clock()

    let camera, scene, raycaster, renderer

    let room

    let controller, controllerGrip
    let INTERSECTED

    init()
    animate()

    function init() {
      const rendererWrapper = document.getElementById('xr-renderer-wrapper')

      scene = new THREE.Scene()
      scene.background = new THREE.Color(0x505050)

      camera = new THREE.PerspectiveCamera(
        50,
        window.innerWidth / window.innerHeight,
        0.1,
        10
      )
      camera.position.set(0, 1.6, 3)
      scene.add(camera)

      room = new THREE.LineSegments(
        new BoxLineGeometry(6, 6, 6, 10, 10, 10).translate(0, 3, 0),
        new THREE.LineBasicMaterial({ color: 0xbcbcbc })
      )
      scene.add(room)

      scene.add(new THREE.HemisphereLight(0xa5a5a5, 0x898989, 3))

      const light = new THREE.DirectionalLight(0xffffff, 3)
      light.position.set(1, 1, 1).normalize()
      scene.add(light)

      const geometry = new THREE.BoxGeometry(0.15, 0.15, 0.15)

      for (let i = 0; i < 200; i++) {
        const object = new THREE.Mesh(
          geometry,
          new THREE.MeshLambertMaterial({ color: Math.random() * 0xffffff })
        )

        object.position.x = Math.random() * 4 - 2
        object.position.y = Math.random() * 4
        object.position.z = Math.random() * 4 - 2

        object.rotation.x = Math.random() * 2 * Math.PI
        object.rotation.y = Math.random() * 2 * Math.PI
        object.rotation.z = Math.random() * 2 * Math.PI

        object.scale.x = Math.random() + 0.5
        object.scale.y = Math.random() + 0.5
        object.scale.z = Math.random() + 0.5

        object.userData.velocity = new THREE.Vector3()
        object.userData.velocity.x = Math.random() * 0.01 - 0.005
        object.userData.velocity.y = Math.random() * 0.01 - 0.005
        object.userData.velocity.z = Math.random() * 0.01 - 0.005

        room.add(object)
      }

      raycaster = new THREE.Raycaster()

      renderer = new THREE.WebGLRenderer({ antialias: true })
      renderer.setPixelRatio(window.devicePixelRatio)
      renderer.setSize(window.innerWidth, window.innerHeight)
      renderer.xr.enabled = true
      rendererWrapper.appendChild(renderer.domElement)

      //

      function onSelectStart() {
        this.userData.isSelecting = true
      }

      function onSelectEnd() {
        this.userData.isSelecting = false
      }

      controller = renderer.xr.getController(0)
      controller.addEventListener('selectstart', onSelectStart)
      controller.addEventListener('selectend', onSelectEnd)
      controller.addEventListener('connected', function (event) {
        this.add(buildController(event.data))
      })
      controller.addEventListener('disconnected', function () {
        this.remove(this.children[0])
      })
      scene.add(controller)

      const controllerModelFactory = new XRControllerModelFactory()

      controllerGrip = renderer.xr.getControllerGrip(0)
      controllerGrip.add(
        controllerModelFactory.createControllerModel(controllerGrip)
      )
      scene.add(controllerGrip)

      window.addEventListener('resize', onWindowResize)

      //

      document.body.appendChild(
        XRButton.createButton(renderer, { optionalFeatures: ['depth-sensing'] })
      )
    }

    function buildController(data) {
      let geometry, material

      switch (data.targetRayMode) {
        case 'tracked-pointer':
          geometry = new THREE.BufferGeometry()
          geometry.setAttribute(
            'position',
            new THREE.Float32BufferAttribute([0, 0, 0, 0, 0, -1], 3)
          )
          geometry.setAttribute(
            'color',
            new THREE.Float32BufferAttribute([0.5, 0.5, 0.5, 0, 0, 0], 3)
          )

          material = new THREE.LineBasicMaterial({
            vertexColors: true,
            blending: THREE.AdditiveBlending
          })

          return new THREE.Line(geometry, material)

        case 'gaze':
          geometry = new THREE.RingGeometry(0.02, 0.04, 32).translate(0, 0, -1)
          material = new THREE.MeshBasicMaterial({
            opacity: 0.5,
            transparent: true
          })
          return new THREE.Mesh(geometry, material)
      }
    }

    function onWindowResize() {
      camera.aspect = window.innerWidth / window.innerHeight
      camera.updateProjectionMatrix()

      renderer.setSize(window.innerWidth, window.innerHeight)
    }

    //

    function animate() {
      renderer.setAnimationLoop(render)
    }

    function render() {
      const delta = clock.getDelta() * 60

      if (controller.userData.isSelecting === true) {
        const cube = room.children[0]
        room.remove(cube)

        cube.position.copy(controller.position)
        cube.userData.velocity.x = (Math.random() - 0.5) * 0.02 * delta
        cube.userData.velocity.y = (Math.random() - 0.5) * 0.02 * delta
        cube.userData.velocity.z = (Math.random() * 0.01 - 0.05) * delta
        cube.userData.velocity.applyQuaternion(controller.quaternion)
        room.add(cube)
      }

      // find intersections

      // TODO
      // raycaster.setFromXRController(controller)

      const intersects = raycaster.intersectObjects(room.children, false)

      if (intersects.length > 0) {
        if (INTERSECTED != intersects[0].object) {
          if (INTERSECTED)
            INTERSECTED.material.emissive.setHex(INTERSECTED.currentHex)

          INTERSECTED = intersects[0].object
          INTERSECTED.currentHex = INTERSECTED.material.emissive.getHex()
          INTERSECTED.material.emissive.setHex(0xff0000)
        }
      } else {
        if (INTERSECTED)
          INTERSECTED.material.emissive.setHex(INTERSECTED.currentHex)

        INTERSECTED = undefined
      }

      // Keep cubes inside room

      for (let i = 0; i < room.children.length; i++) {
        const cube = room.children[i]

        cube.userData.velocity.multiplyScalar(1 - 0.001 * delta)

        cube.position.add(cube.userData.velocity)

        if (cube.position.x < -3 || cube.position.x > 3) {
          cube.position.x = THREE.MathUtils.clamp(cube.position.x, -3, 3)
          cube.userData.velocity.x = -cube.userData.velocity.x
        }

        if (cube.position.y < 0 || cube.position.y > 6) {
          cube.position.y = THREE.MathUtils.clamp(cube.position.y, 0, 6)
          cube.userData.velocity.y = -cube.userData.velocity.y
        }

        if (cube.position.z < -3 || cube.position.z > 3) {
          cube.position.z = THREE.MathUtils.clamp(cube.position.z, -3, 3)
          cube.userData.velocity.z = -cube.userData.velocity.z
        }

        cube.rotation.x += cube.userData.velocity.x * 2 * delta
        cube.rotation.y += cube.userData.velocity.y * 2 * delta
        cube.rotation.z += cube.userData.velocity.z * 2 * delta
      }

      renderer.render(scene, camera)

      const onComponentUnmounted = () => {
        renderer.setAnimationLoop(null)
        window.removeEventListener('resize', onWindowResize)
        window.removeEventListener('component-unm√∑ounted', onComponentUnmounted)
        const xrButton = document.getElementById('XRButton')
        if (xrButton) {
          document.body.removeChild(xrButton)
        }
      }

      // TODO
      window.addEventListener('component-unmounted', onComponentUnmounted)
    }
  },
  // TODO
  unmounted() {
    window.dispatchEvent(new Event('component-unmounted'))
  }
}
</script>

<style lang="scss">
.renderer-wrapper {
  border: 5px #2c3e50 solid;
  height: calc(100% - 256px);
}
</style>
